





















import comm from '../../static/js/common.js';
export default {
	data() {
		return {
			array: ['中医院', '附属医院', '儿童医院', '日本', '美国', '巴西', '日本','美国', '巴西', '日本'],
			index: 0,
			formData: {
				username: '',
				password: ''
			}
		}
	},
	onLoad() {

	},
	methods: {
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value
		},
		formSubmit: function() {
			let that = this;
			if(that.verification()) {
				let param = {
					username: that.formData.username,
					password: that.formData.password
				}
				uni.request({
					url: comm.host_gp + "/auth/login",
					data: param,
					method: 'POST',
					success: function (res) {
						res = res.data;
						if(res.code == 0) {
							uni.setStorageSync('token', res.data.token);
							uni.showToast({
								title: '登录成功',
								icon: 'none',
								duration: 2000
							})
							setTimeout(function() {
								uni.switchTab({
									url: '/pages/index/index'
								})
							}, 100)
						}else {
							uni.showToast({
								title: res.msg,
								icon: 'none',
								duration: 2000
							})
						}
					},
					fail: function (res) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
					}
				})
			};
		},
		verification: function() {
			let that = this;
			if(that.formData.username == '') {
				uni.showToast({
					title: '账号不能为空',
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if(that.formData.password == '') {
				uni.showToast({
					title: '密码不能为空',
					icon: 'none',
					duration: 2000
				});
				return false
			}
			if(that.formData.password.length != 6) {
				uni.showToast({
					title: '密码长度为6位',
					icon: 'none',
					duration: 2000
				});
				return false
			}
			/* if(that.index == '') {
				uni.showToast({
					title: '请选择医院',
					icon: 'none',
					duration: 2000
				});
				return false
			} */
			return true;
		}
	}
}
